<template>
  <div class="home">
    <h1 class="slogon">Customize your <span class="logo-color">LED</span></h1>

    <div class="x-layout">

      <img class="img-one"
        src="https://image.lg-informationdisplay.com/data/product/detail/2024/20240311/1710130348965_16225.jpg" />
      <div class="proposition">
        <h2 class="logo-color">Why choose us?</h2>
        <p>you really need this Imagine you are in finance and you need a model that is smarter about finance than it
          is. You try an open source or private model, but discover that although the models is generally good, it needs
          to become more of an expert in finance.
        </p>

        <div id="chat-container">
          <div id="chat-window">
            <div v-for="message in messages" :key="message.id" class="message"
              :class="{ 'user-message': message.sender === 'user' }">
              <span class="sender">
                {{ message.sender }}:
              </span>
              <span class="msg" style="white-space: pre-line;">
                {{ message.text }}
              </span>
            </div>
          </div>
          <input type="text" v-model="question" @keyup.enter="ask(question)" placeholder="输入你的问题...">
          <button @click="ask(question)">发送</button>
        </div>

      </div>
    </div>

  </div>
</template>

<script>
export default {
  name: 'HomePage',
  components: {
  },
  data() {
    return {
      eventSource: null,
      userInput: '',
      messages: []
    }
  },
  methods: {
    async ask(question) {
      const API_KEY = "fake-key"
      const response = await fetch('http://192.168.1.4:9876/api/gpt/v1/chat/file', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${API_KEY}`
        },
        body: JSON.stringify({
          stream: true,
          question: question,
        })
      })

      // can get id from headers
      this.messages.push({
        id: this.messages.length,
        sender: 'user',
        text: question
      })

      const oldMessages = this.messages

      const reader = response.body.getReader();
      const decoder = new TextDecoder("utf-8");
      let answer = "";

      /* eslint-disable no-constant-condition */
      while (true) {
        const { done, value } = await reader.read();
        if (done) {
          break;
        }
        // Massage and parse the chunk of data
        const chunk = decoder.decode(value);
        const lines = chunk.split("\n");
        const parsedLines = lines
          .map((line) => line.replace(/^data: /, "").trim())
          .filter((line) => line !== "" && line !== "[DONE]") // Remove empty lines and "[DONE]"
          .map((line) => JSON.parse(line));

        for (const parsedLine of parsedLines) {
          const { choices } = parsedLine;
          const { delta } = choices[0];
          const { content } = delta;
          if (content) {
            answer += content;
            this.messages = oldMessages.concat({
              id: oldMessages.length,
              sender: 'bot',
              text: answer
            })
          }
        }
      }
      /* eslint-enable no-constant-condition */
    },
  },
}
</script>

