<template>
  <div class="product">
    <router-link :to="`/product/${product.id}`">
      <img class="big-img" :src="currentImage" alt="empty" />
    </router-link>
    <div class="thumbnail">
      <div v-for="(image, index) in product.images" :key="index">
        <img class="small-img" :src="image" :alt="product.name" @click="showImage(image)" />
      </div>
    </div>
    <div class="info">
      <span class="name">{{ product.name }}</span>
      <span class="stats">
      </span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ProductItem',
  data() {
    return {
      currentImage: ''
    }
  },
  props: {
    product: {
      type: Object,
      required: true
    }
  },
  watch: {
    product: {
      handler() {
        this.currentImage = this.product.images[0];
      },
      immediate: true
    }
  },
  methods: {
    showImage(image) {
      this.currentImage = image;
    },
    showVideo(video) {
      console.log(video);
    }
  }
}

</script>

